import React from 'react';
import { useSelector } from 'react-redux';
import { searchSelector } from '../../redux/store/search/selector';

export const useFormatData = () => {
  const { dateIn } = useSelector(searchSelector);
  let date = dateIn?.split('-').reverse();
  let formatData =
    (date[1] == 1 && date[0] + '  ' + date[2]) ||
    (date[1] == 2 && date[0] + '  ' + date[2]) ||
    (date[1] == 3 && date[0] + '  ' + date[2]) ||
    (date[1] == 4 && date[0] + '  ' + date[2]) ||
    (date[1] == 5 && date[0] + '  ' + date[2]) ||
    (date[1] == 6 && date[0] + '  ' + date[2]) ||
    (date[1] == 7 && date[0] + '  ' + date[2]) ||
    (date[1] == 8 && date[0] + '  ' + date[2]) ||
    (date[1] == 9 && date[0] + '  ' + date[2]) ||
    (date[1] == 10 && date[0] + '  ' + date[2]) ||
    (date[1] == 11 && date[0] + '  ' + date[2]) ||
    (date[1] == 12 && date[0] + '  ' + date[2]);
  return { formatData };
};
